import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';

@Component({
  selector: 'app-services',
  templateUrl: './services.component.html',
  styleUrls: ['./services.component.scss']
})
export class ServicesComponent implements OnInit {

  constructor() { }

  ngOnInit() {
  }

  @ViewChild('custom_data') public custom_data: ElementRef;
  @ViewChild('statistical_data') public statistical_data: ElementRef;
  @ViewChild('analysis_report') public analysis_report: ElementRef;

  public moveToCustomData(): void {
    console.log("click")
    this.custom_data.nativeElement.scrollIntoView({ behavior: 'smooth', block: 'end', inline: 'start' });
  }

  public moveToStatisticalData(): void {
    this.statistical_data.nativeElement.scrollIntoView({ behavior: 'smooth', block: 'end', inline: 'start' });
  }

  public moveToAnalysisReport(): void {
    this.analysis_report.nativeElement.scrollIntoView({ behavior: 'smooth', block: 'end', inline: 'start' });
  }
}
