import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { ContactUsComponent } from './pages/contact-us/contact-us.component';
import { HomeComponent } from './pages/home/home.component';
import { CustomizeSearchComponent } from './pages/customize-search/customize-search.component';
import { CountryCoverComponent } from './pages/country-cover/country-cover.component';
import { ServicesComponent } from './pages/services/services.component';

export const routes: Routes = [
  { path: 'contact-us', component: ContactUsComponent },
  { path: 'home', component: HomeComponent },
  { path: 'services', component: ServicesComponent },
  { path: 'export-import-data', component: CountryCoverComponent },
  { path: 'live-search', component: CustomizeSearchComponent },
  { path: '', redirectTo: 'home', pathMatch: 'full' },
  { path: '**', redirectTo: 'home' }
];

export const routing: ModuleWithProviders = RouterModule.forRoot(routes, { useHash: true, enableTracing: false });
